import Quagga from 'quagga';

import React, { useRef, useEffect } from 'react';

const BarcodeScanner = () => {
  const videoRef = useRef(null);

  useEffect(() => {
    Quagga.init(
      {
        inputStream: {
          name: 'Live',
          type: 'LiveStream',
          target: videoRef.current,
        },
        decoder: {
          readers: [
            'code_128',
            'ean',
            'upc',
            'code_39',
            'codabar',
            'itf',
            'code_93',
            'qr_code',
            'pdf417',
          ],
        },
      },
      (err) => {
        if (err) {
          console.error('Erro ao iniciar o leitor de cdigo de barras:', err);
        } else {
          console.log('Leitor de cdigo de barras iniciado com sucesso');
          Quagga.start();
        }
      }
    );

    Quagga.onDetected((result) => {
      console.log('Cdigo de barras detectado:', result.codeResult.code);
      // Faa algo com o cdigo de barras detectado aqui, por exemplo, enviar para o servidor, exibir na tela, etc.
    });

    return () => {
      Quagga.stop();
    };
  }, []);

  return (
    <>
      <h1>Leitor de cdigo de barras</h1>
      <video ref={videoRef} />
    </>
  );
};

export default BarcodeScanner;
